ks-card {
    display: block;
    position: relative;
    box-sizing: border-box;

    background: #fff;
    color: #151515;

    border: 1px solid #cccccc;
    border-radius: 10px;
    overflow: hidden;
}

ks-card[padding] {
    padding: 40px;
}

@media (max-width: 1200px) {
    ks-card {
        border-radius: 0px;
    }
    
    ks-card[padding] {
        padding: 30px;
    }
}

@media (max-width: 720px) {
    ks-card[padding] {
        padding: 15px;
    }
}

ks-card[padding][small] {
    padding: 15px !important;
}